import React from "react"
import "./list.css"
import ListProduct from "./ListProduct"
import { connect } from "react-redux"
import { asyncActionList } from "../../store/asyncAction";
import * as types from "../../store/type"
class List extends React.Component {
    componentDidMount() {
        this.props.getlist()
    }
    render() {
        return (
            <>
                <div className="list-search_box">
                    <a href="javascript:history.back()" className="list-back lf"></a>
                    <a href="http://yanshi.sucaihuo.com/modals/17/1779/demo/search.html" className="list-search lf">输入商品名称</a>
                </div>
                <div className="list-navbar">
                    <a href="javascript:;" className="list-p_order active">综合</a>
                    <a href="javascript:;" className="list-uptime">新品</a>
                    <a href="javascript:;" className="list-price">
                        <div className="list-sort">
                            <div className="list-sort_up"></div>
                            <div className="list-sort_down"></div>
                        </div>
                        价格
                    </a>
                    <a href="javascript:;" className="list-filter">筛选</a>
                </div>
                <ListProduct list={this.props.list} />
                <div className="list-none">
                    <img src="images/none.png" alt="" />
                    <p>没有您要找的商品哦</p>
                    <p>再搜搜其他商品吧</p>
                </div>
            </>
        )
    }
}
let initMapStateToProps = (state) => ({
    list: state.list
})
let initMapDispatchToProps = (dispatch) => ({
    getlist: () => {
        asyncActionList(
            "http://localhost:3000/page",
            dispatch,
            types.VIEW_LIST
        )
    }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(List);