import React from "react"

let DetailData = (props) => {
    if (!props.detail[0]) {
        return null
    }
    let detail = props.detail[0]
    return (
        <>
            <div className="data">
                <p className="introduce">{detail.title}</p>
                <p className="price">现价：￥{detail.price}</p>
                <p className="scj">原价：<span>￥{detail.oldprice}</span></p>
                <div className="send">
                    <div className="lf">运费：包邮</div>
                    <div className="rf"> 默认配送：圆通速递</div>
                </div>
            </div>
            <div className="warm_prompt">
                温馨提示：海外进口商品经常更换包装盒附件，若更新不及时，敬请谅解！详情里的商品的商品参数、图片、功能及附件仅供参考，请以实物为准。平台保证销售的额进口商品为海外原装正品，请放心选购。
            </div>
            <div className="img_word">
                <p>图文详情</p>
                <img src={detail.img1} alt="" />
                <img src={detail.img2} alt="" />
                <img src={detail.img3} alt="" />
                <img src={detail.img4} alt="" />
            </div>
        </>
    )
}
export default DetailData