import React from "react"
import "./register.css"
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import * as types from "../../store/type"
import { asyncActionUser } from "../../store/asyncAction"
class Reg extends React.Component {
    render() {
        return <>
            <div className="top">
                <a href="javascript:;" className="back lf" onClick={() => { window.history.go(-1) }}></a>
                <span>手机号注册</span>
            </div>
            <form>
                <div className="number">
                    <span>手机号</span>
                    <input type="text" placeholder="请输入手机号" ref="name" />
                </div>
                <div className="validation">
                    <span>验证码</span>
                    <input type="text" placeholder="请输入验证码" />
                    <a className="vali_code" href="javascript:;">获取验证码</a>
                    <a className="time" href="javascript:;">59秒后重发</a>
                </div>
                <div className="pwd">
                    <span>密码</span>
                    <input type="password" placeholder="请输入密码" ref="password" />
                </div>
                <div className="register_btn">
                    <button type="button" onClick={() => {
                        this.props.register(this.refs.name.value, this.refs.password.value)
                    }}>注册</button>
                </div>
            </form>
            <div className="skip">
                <Link to="/login">登录</Link>
            </div>
        </>
    }
}
let initMapStateToProps = (state) => ({
    userdata: state.userdata
})
let initMapDispatchToProps = (dispatch, props) => ({
    register: (username, password) => {
        asyncActionUser(
            "http://localhost:3000/register",
            dispatch,
            types.VIEW_USERDATA,
            {
                name: username,
                password: password
            },
            props
        )
    }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(Reg);