import React from "react"
import "./search.css"
let Search = (props) => (
    <div>
        <div className="s-top">
            <a href="javascript:;" className="s-back lf" onClick={() => { window.history.go(-1) }}></a>
            <input type="text" className="s-search lf" />
            <button type="submit" className="s-search_btn">搜索</button>
        </div>
        <div className="s-main">
            <div className="s-hot_search">
                <p className="s-title">热门搜索</p>
                <div className="s-hot_content">
                    <a className="s-item" href="JavaScript:;">护肤</a>
                    <a className="s-item" href="JavaScript:;">手表</a>
                    <a className="s-item" href="JavaScript:;">钱包</a>
                    <a className="s-item" href="JavaScript:;">香水</a>
                    <a className="s-item" href="JavaScript:;">烟酒</a>
                    <a className="s-item" href="JavaScript:;">保健品</a>
                    <a className="s-item" href="JavaScript:;">手链</a>
                    <a className="s-item" href="JavaScript:;">腰带</a>
                    <a className="s-item" href="JavaScript:;">鞋子</a>
                    <a className="s-item" href="JavaScript:;">戒指</a>
                </div>
            </div>
            <div className="s-history">
                <p className="s-title">历史</p>
                <div className="s-hot_content">
                    <a className="s-item" href="JavaScript:;">&nbsp;</a>
                </div>
            </div>
            <div className="s-empty">
                <a className="s-clear" href="javascript:;">清空历史搜索</a>
            </div>
        </div>
    </div>
)
export default Search
