import React from "react";
import Slider from "../index/Slider";
import Day from "./Day";
import Content from "./Content";
import QueueAnim from "rc-queue-anim"
let Main = (props) => (
    <QueueAnim style={{ display: "block" }} className="main">
        <Slider key="main1" />
        <Day key="main2" />
        <Content key="main3" {...props} />
    </QueueAnim>
)
export default Main;