import React from "react"
import "./shopping_cart.css"
import Buyempty from "./Buyempty";
import { Link } from "react-router-dom"
let Shoppay = (props) => {
    let total = 0;
    let num = 0;
    for (var i = 0; i < props.car.length; i++) {
        total += props.car[i].price * props.car[i].number
        num += props.car[i].number
    }
    return (
        <>
            <div className="buy-pay" >
                <p style={{ width: "295px" }}>合计：￥ <span>{total}.00</span></p>
                <Link to="/mycenter">结算(<span>{num}</span>)</Link>
            </div>
            {props.car.length == 0 && <Buyempty />}
        </>
    )
}
export default Shoppay;