import React from "react"
import "./login.css"
import { connect } from "react-redux"
import { asyncActionLogin } from "../../store/asyncAction"
import * as types from "../../store/type"
import { Link } from "react-router-dom"
class Login extends React.Component {
    render() {
        return (
            <>
                <div className="top">
                    <a href="javascript:;" className="back lf" onClick={() => { window.history.go(-1) }}></a>
                    <span>手机号登录</span>
                </div>
                <form>
                    <div className="number">
                        <span>手机号</span>
                        <input type="text" placeholder="请输入手机号" ref="name" />
                    </div>
                    <div className="pwd">
                        <span>密码</span>
                        <input type="password" placeholder="请输入密码" ref="password" />
                    </div>
                    <div className="login_btn">
                        <button type="button" onClick={() => {
                            this.props.relogin(this.refs.name.value, this.refs.password.value)
                        }}>登录</button>
                    </div>
                </form>
                <div className="skip">
                    <Link className="lf" to="/reg">注册</Link>
                    <a className="rf" href="Javascript:void(0)">忘记密码？</a>
                </div>
            </>
        )
    }
}
let initMapStateToProps = (state) => ({
    userdata: state.userdata
})
let initMapDispatchToProps = (dispatch, props) => ({
    relogin: (username, password) => {
        asyncActionLogin(
            "http://localhost:3000/user",
            dispatch,
            types.VIEW_USERDATA,
            {
                name: username,
                password: password
            },
            props
        )
    }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(Login);