import React from "react"
import { Link } from "react-router-dom"
import QueueAnim from "rc-queue-anim"
let ListProduct = (props) => {
    return (
        <QueueAnim type="top" className="list-list_show" style={{ height: "583px" }}>
            {
                props.list.map((item, index) => {
                    return <div className="list-product_box" key={index}>
                        <div className="list-product">
                            <Link to={{ pathname: "/detail/" + item.id }}>
                                <img src={item.image} alt="" />
                                <p className="list-jieshao">{item.title}</p>
                                <p className="list-jiage">￥{item.price} <span>原价￥{item.oldprice}</span></p>
                            </Link>
                        </div>
                    </div>
                })
            }
        </QueueAnim>
    )
}
export default ListProduct;