import React from "react"
import "./shopping_cart.css"
import { connect } from "react-redux"
import Buymain from "./buymain"
import Shoppay from "./shop-pay";
class Buycar extends React.Component {
    constructor() {
        super()
        this.onoff = true;
    }
    render() {
        return (
            <>
                <div className="buy-top">
                    <a href="javascript:;" className="buy-back" onClick={() => { window.history.go(-1) }}></a>
                    <span>购物车</span>
                    <a href="JavaScript:;" className="buy-edit" onClick={() => { this.edit() }}>编辑</a>
                </div>
                <div className="buy-main" ref="buyMain">
                    <p className="buy-title">全选</p>
                    <Buymain car={this.car} />

                </div>
                <Shoppay car={this.props.buycar} />
            </>
        )
    }
    edit() {

        if (this.onoff) {
            this.refs.buyMain.style.transform = "translateX(-60px)"
            this.refs.buyMain.style.transitionDuration = ".5s"
            this.onoff = false
        } else {
            this.refs.buyMain.style.transform = "translateX(0)"
            this.onoff = true
        }

    }
}
let initMapStateToProps = (state) => ({
    buycar: state.buycar
})
let initMapDispatchToProps = (dispatch) => ({

})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(Buycar);