import React from "react"

let SaleShow = ({ home }) => {
    return (
        <div className="hot_sale_show">
            {home.map((item, index) => (
                <div className="hot_product" key={index}>
                    <img src={item.image} alt="" />
                    <p className="in
                    troduce">{item.name}</p>
                    <p className="price">优惠价：￥{item.price}</p>
                </div>
            ))}
        </div>
    )
}
export default SaleShow;