import React from "react"
import "./my_center.css"
import { connect } from "react-redux";
import * as types from "../../store/type"
class Mycenter extends React.Component {
    constructor(props) {
        super()
        this.userdata = props.userdata
    }
    render() {
        return (
            <div className="My-main">
                <div className="user_box">
                    <div className="user_info">
                        <a href="javascript:;" className="vip_data">
                            <div className="head">
                                <img src="images/0.jpg" alt="" />
                            </div>
                            <div className="vip_right" style={{ width: '268px' }}>
                                <p className="user_name">
                                    XXX <span className="open_vip">开通年卡会员</span>
                                </p>
                                <div className="tjr">
                                    <p>推荐人:</p>
                                    <p className="tjr_name">李威</p>
                                    <p className="tjr_xg" onClick={() => {
                                        this.props.Cancellation()
                                    }}>注销</p>
                                </div>
                                <p className="vip_number" style={{ fontSize: "18px", color: "#f00" }}>会员号:<span>{this.props.userdata.data.name}</span></p>
                                <i className="right_icon"></i>
                            </div>
                        </a>
                    </div>

                    <div className="tjr_xg_tc">
                        <span className="tjr_sp1">修改推荐人</span>
                        <br />
                        <label>请输入推荐人会员号</label>
                        <br />
                        <input className="txt" type="text" name="tjrName" />
                        <br />
                        <p>
                            <span className="tjr_sp2">取消</span>
                            <span>确定</span>
                        </p>
                    </div>
                    <div className="user_order">
                        <a className="dfk" href="javascript:;">
                            <p>0</p>
                            待付款
                            </a>
                        <a className="dsh" href="javascript:;">
                            <p>0</p>
                            待收货
                            </a>
                        <a className="dpj" href="javascript:;">
                            <p>0</p>
                            待评价
                            </a>
                        <a className="tk" href="javascript:;">
                            <p>0</p>
                            退款/售后
                            </a>
                    </div>
                </div>
                <div className="kt_img">
                    <img src="" alt="" />
                </div>
                <div className="vip_rank">
                    <div className="vip_rank_title">
                        <a className="vip_rank_title_link" href="javascript:;">
                            <div className="lf">金卡会员</div>
                            <div className="rf">会员等级</div>
                        </a>
                    </div>
                    <div className="vip_rank_content">
                        <a className="wdfs" href="javascript:;">
                            <p>0</p>
                            我的粉丝
                            </a>
                        <a className="byzd" href="javascript:;">
                            <p>0</p>
                            本月账单
                            </a>
                        <a className="jffd" href="javascript:;">
                            <p>0</p>
                            本月积分返点
                            </a>
                    </div>
                </div>
                <div className="my_purse">
                    <div className="my_purse_title">
                        <a className="my_purse_title_link" href="javascript:;">
                            <b></b>我的钱包
                            </a>
                    </div>
                    <div className="my_purse_content">
                        <a className="freeze" href="javascript:;">
                            <p>0</p>
                            提现冻结
                            </a>
                        <a className="balance" href="javascript:;">
                            <p>0</p>
                            余额
                            </a>
                        <a className="coupon" href="javascript:;">
                            <p>1</p>
                            优惠券
                            </a>
                    </div>
                </div>
                <ul className="item_list">
                    <li className="item">
                        <a className="item_order_link" href="javascript:;">
                            <div className="item_content">
                                全部订单
                                    <span>查看</span>
                            </div>
                        </a>
                    </li>
                    <li className="item">
                        <a className="item_er_link" href="javascript:;">
                            <div className="item_content">
                                推广二维码
                                    <span>点击发送</span>
                            </div>
                        </a>
                    </li>
                </ul>
            </div>
        )
    }
}
let initMapStateToProps = (state) => ({
    userdata: state.userdata
})
let initMapDispatchToProps = (dispatch) => ({
    Cancellation: () => {
        dispatch({ type: types.VIEW_COLLECTION })
    }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(Mycenter)