import React from "react"
import "./detail.css"
import { connect } from "react-redux"
import * as types from "../../store/type"
import { asyncActionDetail } from "../../store/asyncAction";
import DetailData from "./detaildata"
import Banner from "./banner";
import { Link } from "react-router-dom"
class Detail extends React.Component {
    componentDidMount() {
        let id = this.props.match.params.id
        this.props.getdetail(id)
    }
    render() {
        if (!this.props.detail[0]) {
            return null
        }
        let item = this.props.detail[0]
        return (
            <>
                <div className="d-top">
                    <a href="javascript:;" className="back lf" onClick={() => { window.history.go(-1) }}>返回</a>
                    <span>商品</span>
                </div>
                <Banner detail={this.props.detail} />
                <DetailData detail={this.props.detail} />
                <div className="tool_bar">
                    <a className="shangjia" href="javascript:;"></a>
                    <a className="buy_cart" href="javascript:;">
                        <i className="icon_cart">
                            <span className="num">0</span>
                        </i>
                    </a>
                    <Link className="buy" to="/buycar" onClick={() => { this.props.sendCar(item) }}>立即购买</Link>
                    <Link className="join" to="/buycar">加入购物车</Link>
                </div>
            </>
        )
    }
}

let initMapStateToProps = (state) => ({
    detail: state.detail
})
let initMapDispatchToProps = (dispatch) => ({
    getdetail: (id) => {
        asyncActionDetail(
            "http://localhost:3000/detalis",
            dispatch,
            types.VIEW_DETAIL,
            id
        )
    },
    sendCar: (item) => {
        dispatch({
            type: types.VIEW_BUYCAR,
            payload: item
        })
    }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(Detail);