import * as types from "./type";

export let asyncActionList = (url, dispatch, type) => {
    // console.log(url, dispatch, type)
    dispatch({ type: types.VIEW_LOADING, payload: true });
    fetch(
        url,
        // {
        //     credentials: "include",
        //     method: "GET",
        //     headers: new Headers({
        //         'Content-Type': 'application/json'
        //     }),
        //     // body: JSON.stringify(val)
        // }
    ).then(
        res => res.json()

    ).then(
        data => {
            // console.log(data)
            dispatch({ type: types.VIEW_LOADING, payload: false });
            dispatch({ type: type, payload: data })

        }
    )
}
export let asyncActionDetail = (url, dispatch, type, id) => {
    // console.log(url, dispatch, type)
    dispatch({ type: types.VIEW_LOADING, payload: true });
    fetch(
        url,
        {
            credentials: "include",
            method: "POST",
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
            body: JSON.stringify({ id: id })
        }
    ).then(
        res => res.json()

    ).then(
        data => {
            dispatch({ type: types.VIEW_LOADING, payload: false });
            dispatch({ type: type, payload: data })

        }
    )
}
export let asyncActionUser = (url, dispatch, type, user, props) => {
    dispatch({ type: types.VIEW_LOADING, payload: true });
    fetch(
        url,
        {
            credentials: "include",
            method: "POST",
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
            body: JSON.stringify(user)
        }
    ).then(
        res => res.json()

    ).then(
        data => {
            console.log(data)
            dispatch({ type: types.VIEW_LOADING, payload: false });
            if (data.msg == 1) {
                alert("注册成功")
                dispatch({ type: type, payload: data })
                props.history.push("/mycenter")
            } else {
                alert("用户名已存在")
            }
        }
    )
}
export let asyncActionLogin = (url, dispatch, type, user, props) => {
    dispatch({ type: types.VIEW_LOADING, payload: true });
    fetch(
        url,
        {
            credentials: "include",
            method: "POST",
            headers: new Headers({
                'Content-Type': 'application/json'
            }),
            body: JSON.stringify(user)
        }
    ).then(
        res => res.json()

    ).then(
        data => {
            dispatch({ type: types.VIEW_LOADING, payload: false });
            if (data.msg == 1) {
                alert("登陆成功")
                dispatch({ type: type, payload: data })
                props.history.push("/mycenter")
            } else {
                alert("用户名或密码不正确")
            }
        }
    )
}