import React from "react"
import { connect } from "react-redux"
import * as types from "../../store/type"
import QueueAnim from "rc-queue-anim"
class Buymain extends React.Component {

    render() {
        return (
            <QueueAnim className="buy-product" duration={1000}>
                {
                    this.props.buycar.map((item, index) => (
                        <div className="buy-commodity_box" key={index}>
                            <div className="buy-commodity">
                                <div className="buy-img lf">
                                    <img src={item.image} alt="" />
                                </div>
                                <div className="buy-detail lf">
                                    <p className="buy-jieshao">{item.title}</p>
                                    <p className="buy-size">{item.name}</p>
                                    <div className="buy-price_num">
                                        <div className="buy-price lf">
                                            ￥<span>{item.price}</span>
                                        </div>
                                        <div className="buy-num rf">
                                            <button type="button" onClick={() => { this.props.dec(item.id) }}>-</button>
                                            <span>{item.number}</span>
                                            <button type="button" onClick={() => {
                                                this.props.add(item.id)
                                            }}>+</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="buy-del" onClick={() => {
                                this.props.delete(item.id)
                            }}>删除</div>
                        </div>
                    ))
                }
            </QueueAnim>
        )
    }
}

let initMapStateToProps = (state) => ({
    buycar: state.buycar
})
let initMapDispatchToProps = (dispatch) => ({
    add: (id) => {
        dispatch({
            type: types.VIEW_CARADD,
            payload: id
        })
    },
    dec: (id) => {
        dispatch({
            type: types.VIEW_CARDEC,
            payload: id
        })
    },
    delete: (id) => {
        dispatch({
            type: types.VIEW_DELETE,
            payload: id
        })
    }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(Buymain)