import React from "react"
import { NavLink } from "react-router-dom"
let Footer = (props) => (
    <footer>
        <NavLink to="/home" activeClassName="active">首页</NavLink>
        <NavLink to="/list" activeClassName="active">分类</NavLink>
        <NavLink to="/buycar" activeClassName="active">购物车</NavLink>
        <NavLink to="/mycenter" activeClassName="active">我的</NavLink>
    </footer>
)
export default Footer