import React, { Component } from "react"
import { BrowserRouter, HashRouter, Link, NavLink, Route, Redirect, Switch } from "react-router-dom"
import Home from "./index/home"
import Buycar from "./Buycar/buycar";
import Detail from "./detail/detail"
import List from "./list/list"
import Login from "./login/login"
import Reg from "./reg/reg"
import Mycenter from "./Mycenter/Mycenter"
import Footer from "../compon/Footer/Footer";
import Search from "../components/search/search";
import Loading from "../components/loading/Loading"
import connect from "react-redux/lib/connect/connect";
import AuthUser from "../guard/AuthUser"
import * as types from "../store/type"
class App extends Component {
    componentWillReceiveProps(nextprops) {
        if (this.props.location.pathname !== nextprops.location.pathname) {
            let routerPath = nextprops.location.pathname
            if (/home|login|reg|mycenter|list|buycar/.test(routerPath)) {
                this.props.viewFoot(true);
            }
            if (/detail/.test(routerPath)) {
                this.props.viewFoot(false);
            }
        }
    }
    render() {

        return (
            <>
                {this.props.bLoading && <Loading />}
                <Switch>
                    <Route path="/home" component={Home} />
                    <AuthUser path="/buycar" component={Buycar} />
                    <Route path="/detail/:id" component={Detail} />
                    <Route path="/list" component={List} />
                    <Route path="/login" component={Login} />
                    <Route path="/reg" component={Reg} />
                    <AuthUser
                        path="/mycenter"
                        component={Mycenter}
                    />
                    <Route path="/search" component={Search} />
                    <Redirect exact from="/" to="/home" />
                </Switch>
                {this.props.BFoot && <Footer />}
            </>
        )
    }
}
let initMapStateToProps = (state) => ({
    bLoading: state.bLoading,
    BFoot: state.BFoot,

})
let initMapDispatchToProps = (dispatch) => ({
    viewFoot: (bl) => { dispatch({ type: types.VIEW_FOOT, payload: bl }) }
})
export default connect(
    initMapStateToProps,
    initMapDispatchToProps
)(App)





