import React, { Compoment } from "react"
import ReactSwipe from "react-swipe"
class Banner extends React.Component {
    render() {
        if (!this.props.detail[0]) {
            return null
        }
        return (
            <ReactSwipe className="d-carousel" swipeOptions={{
                continuous: true,
                speed: 500,
                auto: 2000,
            }}>
                <div> <img src={this.props.detail[0].img1} alt="" /></div>
                <div> <img src={this.props.detail[0].img2} alt="" /></div>
                <div> <img src={this.props.detail[0].img3} alt="" /></div>
                <div> <img src={this.props.detail[0].img4} alt="" /></div>
            </ReactSwipe>
        )
    }
}
export default Banner;