import * as types from "./type"
let reducer = (state, action) => {
    let { type, payload } = action;
    switch (type) {
        case types.VIEW_LOADING:
            return Object.assign({}, state, {
                bLoading: payload
            });
        case types.VIEW_HOME:

            return Object.assign({}, state, {
                home: payload
            });
        case types.VIEW_LIST:

            return Object.assign({}, state, {
                list: payload
            });
        case types.VIEW_DETAIL:

            return Object.assign({}, state, {
                detail: payload
            });
        case types.VIEW_BUYCAR:
            let arr = [...state.buycar]
            let find = false
            arr.forEach((item, index) => {
                if (item.id == payload.id) {
                    item.number++;
                    find = true;
                }
            });
            if (!find) {
                payload.number = 1;
                arr.push(payload);
            }
            return Object.assign({}, state, {
                buycar: arr
            });
        case types.VIEW_CARADD:
            let arr1 = [...state.buycar]
            for (var i = 0; i < arr1.length; i++) {
                if (payload == arr1[i].id) {
                    arr1[i].number++
                }
            }
            return Object.assign({}, state, {
                buycar: arr1
            });
        case types.VIEW_CARDEC:
            let arr2 = [...state.buycar]
            for (var i = 0; i < arr2.length; i++) {
                if (payload == arr2[i].id) {
                    if (arr2[i].number == 1) {
                        arr2[i].number = 1
                    } else {
                        arr2[i].number--
                    }
                }
            }
            return Object.assign({}, state, {
                buycar: arr2
            });
        case types.VIEW_USERDATA:
            return Object.assign({}, state, {
                userdata: payload
            });
        case types.VIEW_FOOT:
            return Object.assign({}, state, {
                BFoot: payload
            });
        case types.VIEW_DELETE:
            let arr3 = [...state.buycar]
            arr3.map((item, index) => {
                if (payload == item.id) {

                    arr3.splice(index, 1)
                }
            })
            return Object.assign({}, state, {
                buycar: arr3
            });
        case types.VIEW_COLLECTION:
            return Object.assign({}, state, {
                userdata: {}
            });
        default:
            return state;
    }
}
export default reducer